___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --longest-orbit-time: 180s;
}

.overline {
  height: 3px;
  width: 90px;
  --tw-bg-opacity: 1;
  background-color: rgba(255, 255, 255, 1);
  background-color: rgba(255, 255, 255, var(--tw-bg-opacity));
}

@media (min-width: 1024px) {

.overline {
    height: 5px;
  }
  }

.accent {
  mix-blend-mode: saturation;
  position: absolute;
  z-index: -10;
  width: 100%;
  overflow: hidden;
}

.accent > div {
  background: radial-gradient(
    #394389,
    #394389,
    #1D2030,
    #1D2030
  );
  height: 650px;
  width: 650px;
}

@media (min-width: 1024px) {

.accent > div {
    width: 1300px;
    height: 1300px;
}
  }

.circle {
  position: absolute;
  height: 48px;
  width: 48px;
  border-radius: 9999px;
  --tw-bg-opacity: 1;
  background-color: rgba(189, 192, 211, 1);
  background-color: rgba(189, 192, 211, var(--tw-bg-opacity));
}

@media (min-width: 1024px) {

.circle {
    width: 110px;
    height: 110px;
  }
  }

.values-illustration {
  transform: scale(1.2) rotate(-35deg);
}

.planet {
  animation:
    move linear infinite,
    hide linear infinite;
  transform-style: preserve-3d;
  z-index: 1;
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  border-radius: 9999px;
}

#mercury {
  animation-duration: 180s;
  animation-duration: var(--longest-orbit-time);
  offset-path: ellipse(42.5% 13.25% at 55.5% 42.5%);
  height: 16px;
  width: 16px;
  --tw-bg-opacity: 1;
  background-color: rgba(255, 70, 0, 1);
  background-color: rgba(255, 70, 0, var(--tw-bg-opacity));
}

#venus {
  animation-duration: calc(0.8 * 180s);
  animation-duration: calc(0.8 * var(--longest-orbit-time));
  animation-delay: calc(-0.25 * 180s);
  animation-delay: calc(-0.25 * var(--longest-orbit-time));

  offset-path: ellipse(36.25% 11.25% at 55.5% 42.5%);

  height: 14px;

  width: 14px;

  --tw-bg-opacity: 1;

  background-color: rgba(255, 122, 0, 1);

  background-color: rgba(255, 122, 0, var(--tw-bg-opacity));
}

#earth {
  animation-duration: calc(0.6 * 180s);
  animation-duration: calc(0.6 * var(--longest-orbit-time));
  animation-delay: calc(-0.25 * 180s);
  animation-delay: calc(-0.25 * var(--longest-orbit-time));

  offset-path: ellipse(30% 9% at 55.5% 42.5%);

  height: 10px;

  width: 10px;

  --tw-bg-opacity: 1;

  background-color: rgba(247, 166, 0, 1);

  background-color: rgba(247, 166, 0, var(--tw-bg-opacity));
}

#mars {
  animation-duration: calc(0.4 * 180s);
  animation-duration: calc(0.4 * var(--longest-orbit-time));
  animation-delay: calc(-0.85 * 180s);
  animation-delay: calc(-0.85 * var(--longest-orbit-time));

  offset-path: ellipse(24.25% 7% at 55.5% 42.5%);

  height: 8px;

  width: 8px;

  --tw-bg-opacity: 1;

  background-color: rgba(255, 239, 91, 1);

  background-color: rgba(255, 239, 91, var(--tw-bg-opacity));
}

@keyframes move {
  0% {
    offset-distance: 0%;
  }
  100% {
    offset-distance: 100%;
  }
}

@keyframes hide {
  0% {
    z-index: 15;
  }
  50% {
    z-index: 15;
  }
  51% {
    z-index: 1;
  }
  100% {
    z-index: 1;
  }
}
