___CSS_LOADER_EXPORT___.push([module.id, `#navigation {
  transition: right 0.2s cubic-bezier(0.33, 1, 0.68, 1);
  right: -100vw;
  position: fixed;
  top: 0px;
  z-index: 50;
  display: flex;
  height: 100%;
  width: 100%;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  overflow: hidden;
  --tw-bg-opacity: 1;
  background-color: rgba(248, 166, 0, 1);
  background-color: rgba(248, 166, 0, var(--tw-bg-opacity));
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

#navigation a {
  font-size: 1.5rem;
  font-weight: 700;
  -webkit-text-decoration: none;
  text-decoration: none;
  position: relative;
  width: -moz-fit-content;
  width: fit-content;
  padding-bottom: 1rem;
  text-align: right;
  text-transform: capitalize;
  --tw-text-opacity: 1;
  color: rgba(29, 32, 48, 1);
  color: rgba(29, 32, 48, var(--tw-text-opacity));
}

#navigation a:after {
  background: none repeat scroll 0 0 transparent;
  content: "";
  display: block;
  position: absolute;
  width: 0;
  height: 2px;
  left: 50%;
  bottom: 8px;
  background: #131520;
  transition:
    width 0.3s ease, left 0.3s ease;
}

#navigation a:hover:after {
  width: 100%;
  left: 0;
}

#navigation p {
  font-weight: 500;
  text-align: right;
  --tw-text-opacity: 1;
  color: rgba(29, 32, 48, 1);
  color: rgba(29, 32, 48, var(--tw-text-opacity));
}
